.friendList {
  list-style: none;
  display: flex;
  flex-direction: column;
}
.item {
  width: 300px;
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 0 5px 5px #333;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  padding: 15px 25px;
}

.status {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  margin-right: 15px;
}
.avatar {
  margin-right: 15px;
}
.name {
  font-size: 26px;
  margin: 0;
}
